import React, {useEffect, useState} from 'react';
import './App.css';
import {MainComponent, MainComponentInterface} from "./components/mainPage/mainComponent";
import {Subject} from "rxjs";
import {Icon} from "semantic-ui-react";
import SettingsModal, {AvatarInterface, StyleInterface} from "./components/Modals/SettingsModal/SettingsModal";
import {getModels} from "./servers/services/sessionService";

export const settingsSubject = new Subject<StyleInterface>()
export const avatarSubject = new Subject<AvatarInterface>()

function App() {
    const avatarId = "Kristin_public_2_20240108";
    const voiceId = "71b0aa6499f6458e8b040818a017db1f";


    const[style,setStyle] = useState<StyleInterface>({background:"data:image/webp;base64,"
    ,
    color:"#4caf50"});
    const [avatar,setAvatar] = useState<AvatarInterface>({avatarId: avatarId, voiceId: voiceId});
    const [open,setOpen] = useState<boolean>(false)

    const [load,setLoad] = useState(false);
    const [reRender,setReRender] = useState(false);
    const [main,setMain] = useState<MainComponentInterface>({avatar:avatar,reRender:reRender,setReRender:setReRender,style})

    useEffect(() => {
        const timer = setTimeout(() => {
            setLoad(true);
        }, 3000); // 3000 milisecunde = 3 secunde

        // Curățarea timerului la demontarea componentei
        return () => clearTimeout(timer);
    }, []);


    useEffect(() => {
        if(load){
            const setting=localStorage.getItem("localSettings")
            if(setting){
                setStyle(JSON.parse(setting));
            }

            avatarSubject.subscribe({next:(value)=>{
                setAvatar(value);
                setReRender(true);
                }})
            settingsSubject.subscribe({next:(value)=>{
                setStyle(value);
                }})
        }
    }, [load]);

    return (
        <div className={"mainBody"}>
            <div style={{position: "relative", overlay: "none"}}><SettingsModal open={open} set={setOpen}/></div>
            <div className={"mainContainer"}>
                <div className={"settingsDisplay"} onClick={() => {
                    setOpen(true);
                }}>
                    <Icon name={"settings"} className={"settingsIcon"}/>
                </div>

                <div className={"mainAppDisplay"} style={{background:`url(${style.background})`,borderColor:`${style.color}`}}>
                    <MainComponent avatar={avatar} reRender={reRender} setReRender={setReRender} style={style}></MainComponent>
                </div>
            </div>
        </div>
    );
}

export default App;
