import "./SettingsModal.css"
import {Button, Divider, Icon, Input, Label, Modal, ModalActions, ModalContent, ModalHeader} from "semantic-ui-react";
import React, {useEffect, useState} from "react";
import {avatarSubject, settingsSubject} from "../../../App";
import DropdownButton from "../../input/DropDown";
import {getModels} from "../../../servers/services/sessionService";
import {DropdownSelectionButton} from "../../input/button/Dropdown";

export interface StyleInterface{
    color:string,
    background:string,
}


export interface LocalSettings {
    color:string,
    background:string,
    lang:string,
    message:string,
}

export interface AvatarInterface{
    avatarId:string,
    voiceId:string,
}


function SettingsModal(props: any) {
    const [inputMessage,setMessage] = useState<string>("hi")
    const [color,setColor] = useState<string>("#2b542c")


    const avatarId = "Kristin_public_2_20240108";
    const voiceId = "71b0aa6499f6458e8b040818a017db1f";
    const [open, setOpen] = useState<boolean>(false);
    const [bck,setBck] = useState<StyleInterface>({
        background:"data:image/webp;base64,"
        ,
        color:"#2b542c"});
    const [avatar,setAvatar] = useState<AvatarInterface>({avatarId: avatarId, voiceId: voiceId});
    const [localSett,setLocalSettings] = useState<LocalSettings>()
    const [bckt,setBckt] = useState<string>(bck.background);
    const [models, setModels] = useState<string[]>([])
    const [modelOptions, setModelOptions] = useState([{key:"",value:"",text:""}])

    useEffect(() => {
        getModels()
            .then((data)=>{
            return data.json()
        }).then((data)=>{
            console.log(JSON.parse(data.text))
            setModels(JSON.parse(data.text))

        })

        // console.log(props)
        const settings = localStorage.getItem("localSettings");
        if (props.open === true) {
            if(settings){
                setLocalSettings(JSON.parse(settings))
            } else {
                const localDummy:LocalSettings = {background: bck.background, color: bck.color, lang: "en", message: "Hi!"}
                setLocalSettings(localDummy)
            }
            setOpen(props.open);
        }
    }, [props.open]);

    useEffect(() => {
        const list = []
        for(const m of models){
            list.push({key:m,value:m,text:m})
        }
        setModelOptions(list)
    }, [models]);



    return (
        <div>
            <Modal className={"modal"}
                   dimmer={"blurring"}
                   open={open}
                // onClose={() => dispatch({ type: 'CLOSE_MODAL' })}
            >
                <ModalHeader>Settings</ModalHeader>
                <ModalContent>
                    <div style={{
                        width: "80%",
                        position: "relative",
                        height: "500px",
                        overflowX:"scroll"
                    }}>
                        <Label>Avatar id</Label>
                        <Input  placeholder='Enter avatar ui ID...' className={"avatarInput"}  onChange={(value)=>{
                            const dummy:AvatarInterface = {avatarId: value.target.value, voiceId: avatar.voiceId}
                            setAvatar(dummy)
                        }}/>
                        <Label>Voice id</Label>
                        <Input placeholder='Enter avatar ui Voice...' className={"avatarInput"} onChange={(value)=>{
                            const dummy:AvatarInterface = {avatarId: avatar.avatarId, voiceId: value.target.value}
                            setAvatar(dummy)
                        }}/>

                        <Button className={"sessionButton"} onClick={() => {
                            avatarSubject.next(avatar);
                        }}><Icon name={"redo"} className={"reloadIcon"}/></Button>
                        <Divider/>
                        <Label>Background</Label>
                        <Input placeholder='Enter new url...' value={bckt} className={"avatarInput"} onChange={(value)=>{
                            setBckt(value.target.value)
                            const dummy:StyleInterface = {
                                background: value.target.value, color: bck?.color?bck.color:""
                            }
                            setBck(dummy);
                            const set = localSett;
                            if(set) {
                                set.background = value.target.value;
                                setLocalSettings(set);
                            }
                        }}/>
                        <Label>Colors</Label>
                        <Input type={"color"} value={color} onChange={(value)=>{
                            setColor(value.target.value)
                            const dummy:StyleInterface = {
                                background: bck?.background ? bck?.background:" ", color: value.target.value

                            }
                            setBck(dummy);
                            const set = localSett;
                            if(set) {
                                set.color = value.target.value;
                                setLocalSettings(set);
                            }
                        }} className={"avatarInput"}/>
                        <DropdownButton onSelectChange={(value:any)=>{
                            const set = localSett;
                            if(set) {
                                set.lang = value;
                                setLocalSettings(set);
                            }
                        }}/>
                        <br/>
                        <DropdownSelectionButton name={"Model:"} onSelectChange={(value:string)=>{
                            localStorage.setItem("model",value)
                        }} options={modelOptions}/>
                        <br/>
                        <Input placeholder='Message...' value={inputMessage} className={"avatarInput"} onChange={(value)=>{
                            setMessage(value.target.value)
                            const set = localSett;
                            if(set) {
                                set.message = value.target.value;
                                setLocalSettings(set);
                            }
                        }}/>
                    </div>
                </ModalContent>
                <ModalActions>
                    <Button negative onClick={() => {
                        setOpen(false)
                        props.set(false)
                    }}>
                        Don't save
                    </Button>
                    <Button positive onClick={() => {
                        if (bck) {
                            settingsSubject.next(bck);
                        }

                        if(localSett) {
                            localStorage.setItem("localSettings",JSON.stringify(localSett))
                        }

                        setOpen(false)
                        props.set(false)
                    }}>
                        Save
                    </Button>
                </ModalActions>
            </Modal>
        </div>
    )
}

export default SettingsModal